@import url(https://fonts.googleapis.com/css2?family=Baloo+Bhai+2:wght@400;500;600;700;800&family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body{
    background-color: rgb(185, 255, 255);
    font-family: 'Baloo Bhai 2';
}
/* ================== Utility ================= */

.flex{
    display: flex;
    justify-content: space-between !important;
    align-items: center;
}
.disable{
    display: none;
}
/* ================== DarkBtn box Theme ================= */

.bi-brightness-high-fill{
    color: hsla(54, 100%, 50%, 1) ;
    font-size: 1.4rem;
    padding: 2px 6px;
}
.bi-moon-stars-fill{
    color: rgb(185, 255, 255) ;
    font-size: 1.4rem;
    padding: 2px 6px;
}
.theme-button{
    padding: 0 !important;
}
/* ================= TextArea ========== */
#myTextArea{
    font-size: 1.4rem;
}
.navbar-brand{
    font-size: 1.5rem;
    font-family: "Pacifico";
    font-weight: 600;
}

/* ================== Color box Theme ================= */
.right-side-theme-box{
    padding:1rem;
    margin-right: 1.5rem;
    border-radius: 50%;
    background:linear-gradient(180deg,red,blue,green,yellow,purple) ;
    cursor: pointer;
}
/* ================== Accordian box Theme ================= */

.hidden{
    display: none;
}
.visible{
    display: block;
}

.accordion{
    border: 2px solid transparent;
    background-color: White ;
}
.accordion-dark{
    background-color: #050410;
}
/* ================== Text Area box Theme ================= */
.textarea-dark{
    background-color: #050410 !important;
    color: white !important;
}

/* ====================button===================== */
.button{
    border: 2px solid transparent !important;
    color: white !important;
    font-weight: 600;
    font-size: 1.1rem;
    margin: 5px 0px;
    margin-right: 21px ;
    width: auto;
    /* width: 110px; */
    /* padding: 8px 0px !important; */
}

.button1{
    background-color: hsla(209, 100%, 50%, 1);
}

.button2{
    color: black !important;
    background-color: hsla(54, 100%, 50%, 1);
}
.button3{
    background-color: hsla(266, 100%, 50%, 1);
}
.button4{
    background-color: hsla(360, 100%, 51%, 1);
}
.button5{
    background-color: rgb(3, 160, 180);
}
.button6{
    background-color: hsla(251, 100%, 50%, 1);
}

.button1:hover{
    background-color:hsla(209, 100%, 40%, 1) ;
}

.button2:hover{
    background-color:hsla(54, 100%, 45%, 1);
    color: black;
}
.button3:hover{
    background-color: hsla(266, 100%, 30%, 1);
}
.button4:hover{
    background-color: hsla(360, 100%, 40%, 1);
}
.button5:hover{
    background-color: rgb(3, 160, 180);
}
.button6:hover{
    background-color: hsla(251, 100%, 40%, 1);

}

.button-box{
    display: flex;
    flex-wrap: wrap;
    margin:21px 0px;
    margin-bottom: 41px;
    align-items: center;
    /* justify-content: space-between; */
}



